<template>
    <header>
        <div class="l-content">
            <i :class="[$store.state.tab.isCollapse? 'el-icon-s-fold':'el-icon-s-unfold','isfold']" @click="collapseMenu"></i>
            <el-breadcrumb separator="/">
                <!--很明显 首页 一定是存在的 所以这里直接写死-->
                <el-breadcrumb-item :to="{ path: '/' }">主页</el-breadcrumb-item>
                <!--第二级菜单名称 就要看左侧组件有没有点击指定菜单，没有那就只显示首页 点击就显示当前菜单名称-->
                <el-breadcrumb-item :to="current.path" v-if="current">{{current.Label}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="r-content">
            <el-dropdown trigger="click" size="mini" class="drop_name">
                <div class="el-dropdown-link">
        
                    <div class="name_c">
                        <span class="myname">{{personform.Name}}</span>
                        <img :src="userImg" class="user" />
                    </div>
                </div>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="personinfo">
                        <div class="infoitem">个人中心</div>
                    </el-dropdown-item>
                    <el-dropdown-item @click.native="editpassword">
                        <div class="infoitem">修改密码</div>
                    </el-dropdown-item>
                    <el-dropdown-item @click.native="logOut">
                        <div class="infoitem">退出</div>
                    </el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>

        <!-- 修改密码 -->
        <el-dialog title="修改密码"  width="25%" :visible.sync="passworddialog" @close="resetVerForm">
            <el-form :model="mimaform" :rules="rules" ref="mimaForm">
                <!-- <el-form-item label="用户名" label-width="80px">
                    <span>{{personform.name}}</span>
                </el-form-item> -->
                <el-form-item label="旧密码" label-width="80px">
                    <el-input v-model="mimaform.password" autocomplete="off" ref="oldmima"></el-input>
                </el-form-item>
                <el-form-item label="新密码" label-width="80px" prop="newPassword">
                    <el-input v-model="mimaform.newPassword" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" label-width="80px" prop="newPasswordcomed">
                    <el-input v-model="mimaform.newPasswordcomed" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="gotosave('mimaForm')" :loading="loading1">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 个人中心 -->
        <el-dialog title="个人资料" :visible.sync="persondialog" width="30%">
            <el-form :model="personform" ref="infoForm">
                <el-form-item label="用户名" label-width="80px">
                    <el-input v-model="personform.Name"  disabled></el-input>
                </el-form-item>
                <el-form-item label="账号" label-width="80px">
                    <el-input v-model="personform.Mobile" disabled></el-input>
                </el-form-item>
                <el-form-item label="创建时间" label-width="80px">
                    <el-input v-model="personform.CreatedAt" disabled></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="gotosave2('infoForm')" :loading="loading2">关 闭</el-button>
            </div>
        </el-dialog>
    </header>
</template>

<script>
import { mapState } from "vuex";
import { isEmail, isMobile } from "@/utils/validate";
export default ({
    computed: {
        ...mapState({
            current: state => state.tab.currentMenu
        })
    },
    data() {
        var checkpass = (rule, value, callback) => {
            if (value === "") {
                callback(new Error("请再次输入新密码"));
            } else if (value !== this.mimaform.newPassword) {
                callback(new Error("两次输入密码不一致!"));
            } else {
                callback();
            }
        };
        return {
            userImg: require("../assets/images/user.jpg"),
            OrganizationName:'',
            passworddialog: false,
            mimaform: {
                password: "",
                newPassword: "",
                newPasswordcomed: ""
            },
            rules: {
                newPassword:[{pattern:/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,20}$/, message: '密码必须包含大小写字母和数字，字符8-20位之间', trigger: 'blur'}],
                newPasswordcomed: [{ validator: checkpass, trigger: "blur" }]
            },
            persondialog: false,
            personform: {
                Name: "",
                CreatedAt: "",
                Mobile: "",
                Role: "",
                ID: ""
            },
            loading1: false,
            loading2: false
        };
    },
    inject: ["routerRefresh"],
    created() {
        this.getUserinfo();
        console.log(this.current)
    },
    mounted() {
        this.db_password = this.$_double.debounce(this.savepassword, 500, {
            leading: true, //点击立即触发
            trailing: false //连续点击完再触发
        });

        this.db_info = this.$_double.debounce(this.saveinfo, 500, {
            leading: true, //点击立即触发
            trailing: false //连续点击完再触发
        });
    },
    methods: {
        // 获取个人信息
        getUserinfo() {
            this.apiSever.get(`${this.URl.ADMIN_INFO}`, res => {
                this.personform = res.data;
            });
        },
        //控制左侧菜单是否折叠
        collapseMenu() {
            this.$store.commit("collapseMenu");
        },
        //退出登陆
        logOut() {
            this.apiSever.post(this.URl.LOGOUT,{},res=> {
               if(res.status ==200){
                    this.$message.success("退出成功！");
                    this.$cookies.remove("mytoken");
                    localStorage.clear();
                    location.reload();
                }
            })
        },
        // 修改密码
        editpassword() {
            this.loading1 = false;
            this.passworddialog = true;
        },
        resetVerForm() {
          this.mimaform = {
            password: "",
            newPassword: "",
            newPasswordcomed: ""
          };
        this.loading1 = false;
        this.passworddialog = false;
        this.$nextTick(() => {
          this.$refs["mimaForm"].resetFields();
        })
         
      },
        // 保存密码
        gotosave(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.loading1 = true;
                    this.db_password();
                    this.resetVerForm()
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        savepassword() {
            let obj = {
                Password: this.mimaform.password,
                NewPassword: this.mimaform.newPassword
            };
            this.apiSever.post(this.URl.ADMIN_CHANGE_PASSWORD,obj,res=> {
                if(res.status == 200){
                    this.$message.success("修改密码成功！");
                }
                this.passworddialog = false;
            })
            
        },
        // 个人中心
        personinfo() {
            this.persondialog = true;
            this.getUserinfo()
        },
        // 修改个人资料
        gotosave2() {
            this.persondialog = false;
            // this.loading2 = true;
            // this.db_info();
        },
        saveinfo() {
            this.$axios
                .post("/admin/changeInfo", this.personform)
                .then(res => {
                    if (res.data.status == 1) {
                        this.$message.success("修改资料成功！");
                        this.persondialog = false;
                    } else {
                        this.$message.error(res.data.message);
                    }
                })
                .finally(() => {
                    this.loading2 = false;
                });
        }
    }
})
</script>

<style lang="less" scoped>
header /deep/ .el-dialog__body {
    padding: 0 20px;
}
.infoitem {
    width: 200px;
    text-align: center;
    line-height: 25px;
    font-size: 18px;
    padding: 10px 0;
}
header {
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: space-between;
}

.l-content {
    display: flex;
    align-items: center;
    .el-button {
        margin-right: 20px;
    }
    /deep/ .el-breadcrumb__inner{
        color: #333333!important;
    }
    .isfold {
        font-size: 25px;
        color: #333333;
        margin-right: 15px;
        cursor: pointer;
    }
}

.r-content {
    margin-right: 20px;
    .name_c {
        display: flex;
        cursor: pointer;
        align-items: center;
    }
    .myname {
        color: #333333;
        display: inline-block;
        line-height: 45px;
        font-size: 16px;
        margin-right: 10px;
        font-weight: bold;
    }
    .user {
        width: 35px;
        height: 35px;
        border-radius: 50%;
    }
}
</style>

<style lang="scss">
.el-breadcrumb__item {
    .el-breadcrumb__inner {
        color: #666666;
        font-weight: normal;
    }

    &:last-child {
        .el-breadcrumb__inner {
            color: #ffffff;
        }
    }
}
</style>
