<template>
    <div class="tabs">
        <!--closable这里说明home是不能关闭的-->
        <el-tag :key="tag.Name" size="small" v-for="(tag, index) in tags" :closable="tag.Name !== 'home'" :disable-transitions="false" @close="handleClose(tag, index)" @click="changeMenu(tag)" :effect="$route.name === tag.Name ? 'dark' : 'plain'">
            {{ tag.Label }}
        </el-tag>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
    computed: {
        ...mapState({
            tags: state => state.tab.tabsList
        })
    },
    methods: {
        ...mapMutations({
            close: "closeTab"
        }),
        //关闭标签
        handleClose(tag, index) {
            let length = this.tags.length - 1;
            //vuex调方法的另一种写法
            this.close(tag);
            // 如果关闭的标签不是当前路由的话，就不跳转
            if (tag.Name !== this.$route.name) {
                return;
            }
            // 关闭的标签是最右边的话，往左边跳转一个
            if (index === length) {
                this.$router.push({ name: this.tags[index - 1].Name });
            } else {
                // 否则往右边跳转
                this.$router.push({ name: this.tags[index].Name });
            }
        },

        //选择标签跳转路由
        changeMenu(item) {
            this.$store.commit("selectMenu", item);
            this.$router.push({ name: item.Name});
        }
    }
};
</script>

<style lang="scss" scoped>
.tabs {
    background-color: #edf2f5;
    padding: 10px 20px 0px !important;
    .el-tag {
        margin-right: 15px;
        margin-bottom: 10px;
        cursor: pointer;
    }
}
</style>
