<template>
    <!--collapse 是否水平折叠收起菜单-->
    <el-menu :collapse="isCollapse" :default-active="$route.path" class="el-menu-vertical-demo" background-color="#2d61aa" text-color="#fff" active-text-color="#ffd12d"  :default-openeds="defaultOpeneds" @select="selectedmenu">
        <!--是否水平折叠收起菜单 会影响这里字段的显示 -->
        <h3 v-show="isCollapse"></h3>
        <h3 v-show="!isCollapse">auth-gateway</h3>
        <template v-if="!isCollapse">
            <template v-for="(menu1,index1) in menu">
                <el-submenu :index="menu1.Path" v-if="menu1.Children && menu1.Children.length>0" :key="index1" :title="menu1.Label">
                    <template slot="title">
                        <img :src="require('../assets/images/'+menu1.Icon+'.png')"  class="icon-titlle">
                        <!-- <i :class="menu1.Icon"></i> -->
                        <span>{{menu1.Label}}</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item :index="menu2.Path" v-for="(menu2,index2) in menu1.Children" :key="index2" @click="clickMenu(menu2)" :title="menu2.Label">
                            {{menu2.Label}}
                        </el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-menu-item :key="index1+'no'" v-else :index="menu1.Path" @click="clickMenu(menu1)" :title="menu1.Label">
                    <!-- <i :class="menu1.Icon"></i> -->
                    <img :src="require('../assets/images/'+menu1.Icon+'.png')"  class="icon-titlle">
                    {{menu1.Label}}
                </el-menu-item>
            </template>
        </template>
    </el-menu>
</template>

<script>
export default {
    //计算属性
    computed: {
        // //没有子菜单
        // noChildren() {
        //     return this.menu.filter(item => {
        //         return item.children.length == 0;
        //     });
        // },
        // //有子菜单 (这样设置会有一个问题 就是有子菜单的 永远会在没有子菜单的下面
        // hasChildren() {
        //     return this.menu.filter(item => {
        //         return item.children.length > 0;
        //     });
        // },
        isCollapse() {
            // 这里的数据就是从vuex取得
            return this.$store.state.tab.isCollapse;
        }
    },
    data() {
        return {
            defaultOpeneds:[],
            menu: [ ]
        };
    },
    created() {
        this.getMenu();
    },
    mounted() {
       
    },
    methods: {
        //跳转路由 根据名称跳转
        clickMenu(item) {
            // this.$store.commit("closeTabAside", item);
            this.$router.push({ name: item.Name });
            // this.$store.commit("selectMenuAside", item);
            this.$store.commit("selectMenu", item);
           
        },
        getMenu() {
            // this.apiSever.get(`${this.URl.MENU+'?Type=My'}`, res => {
            this.apiSever.get(`${this.URl.MENU+'?Type=My'}`, res => {
                this.menu = res.data;
                this.defaultData()
            });
        },
        selectedmenu(index, indexPath) {
            // console.log(11111111,index,indexPath);
        },
        defaultData(){//全部展开
            // this.defaultOpeneds=[]
            // this.menu.forEach((item,index)=>{
            //     if(item.Children && item.Children.length>0){
            //         this.defaultOpeneds.push(String(item.Path))
            //     }
            // })
        },
    }
};
</script>

<style lang="less" scoped>
.el-menu {
    height: 100%;
    border: none;
    h3 {
        color: #ffffff;
        text-align: center;
        // line-height: 48px;
        font-weight: bold;
    }
    /deep/ .el-menu-item,
    /deep/ .el-submenu__title {
        font-size: 16px;
        height: 45px;
        line-height: 45px;
    }
    /deep/ .el-menu-item-group__title {
        display: none;
    }
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 220px;
    min-height: 400px;
}
.icon-titlle{
    width: 20px;
    margin-right: 8px;
    vertical-align: middle;
}

.el-menu-vertical-demo /deep/ .is-active {
    background-color: rgba(31, 45, 61, 0.5) !important;
}
</style>
